import React, { useState } from "react";
import {
  Footer,
  FooterContainer,
  MainWrapper,
  SelectButton,
  SubmitButton,
} from "./main.styles";
import { NavBar } from "../components/navBar/navBar";
import CarSrc from "../assets/car.png";
import BonnetSrc from "../assets/Bonnet.png";
import FrontDSrc from "../assets/frontDoor.png";
import RearDSrc from "../assets/rearDoor.png";
import BufferSrc from "../assets/buffer.png";
import { Slider } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";
import {
  getFibreMaterials,
  getFibreMaterialsEnergy,
  manufacturingProcess,
  manufacturingProcessEnergy,
  resinMaterials,
} from "./data";
import { useRef } from "react";
import { useEffect } from "react";

const useStyles = makeStyles({
  root: {
    color: "#1e1f1c", // Your custom color here
  },
});

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const colors = ["#4caf50", "#607d8b", "#795548", "#ff9800"];

const labelsConst = ["Option 1", "Option 2", "Option 3", "Option 4"];

const defaultCo2Data = {
  labels: labelsConst,
  datasets: [
    {
      label: "Greenhouse Gas Emissions (kg CO₂ eq.)",
      data: [],
      backgroundColor: colors,
    },
  ],
};

const defaultEnergyData = {
  labels: labelsConst,
  datasets: [
    {
      label: "Cumulative Energy Demand (MJ)",
      data: [],
      backgroundColor: colors,
    },
  ],
};

const options = {
  responsive: true,
  plugins: {
    legend: {
      display: false,
    },
    title: {
      display: true,
      text: "Greenhouse Gas Emissions (kg CO₂ eq.)",
    },
  },
};

const optionsEnergy = {
  responsive: true,
  plugins: {
    legend: {
      display: false,
    },
    title: {
      display: true,
      text: "Cumulative Energy Demand (MJ)",
    },
  },
};

export function CarModel2Page(props) {
  const classes = useStyles();
  const scrollRef = useRef(null);
  const [selectSrc, setSelectedSrc] = useState(BonnetSrc);
  const [selectPart, setSelectedPart] = useState("BONNET");
  const [selectedFibre, setSelectedFibre] = useState(0);
  const [selectedResin, setSelectedResin] = useState(0);
  const [selectedProcess, setSelectedProcess] = useState(0);
  const [submittedOptions, setSubmittedOptions] = useState([]);
  const [rwvalue, setRWValue] = useState(1);
  const [chartData, setChartData] = useState(defaultCo2Data);

  const [energyData, setEnergyData] = useState(defaultEnergyData);

  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkIsMobile = () => {
      const mediaQuery = window.matchMedia("(max-width: 426px)"); // Adjust the media query as needed for your definition of mobile
      setIsMobile(mediaQuery.matches);
    };

    checkIsMobile(); // Initial check

    window.addEventListener("resize", checkIsMobile); // Check on window resize

    return () => {
      window.removeEventListener("resize", checkIsMobile);
    };
  }, []);

  const changeRWValue = (event, value) => {
    setRWValue(value);
  };
  const getText = (valu) => `${rwvalue}`;

  let cor = "";
  const onImageClick = (e) => {
    cor = cor + `,${e.clientX - 98},${e.clientY - 112}`;
    console.log(cor);
  };
  const onFrontDClick = (e) => {
    if (selectSrc !== FrontDSrc) {
      setSubmittedOptions([]);
      setChartData(defaultCo2Data);
      setEnergyData(defaultEnergyData);
      setSelectedSrc(FrontDSrc);
      setSelectedPart("FRONT DOOR");
    }
  };
  const onRearDClick = (e) => {
    if (selectSrc !== RearDSrc) {
      setSubmittedOptions([]);
      setChartData(defaultCo2Data);
      setEnergyData(defaultEnergyData);
      setSelectedSrc(RearDSrc);
      setSelectedPart("REAR DOOR");
    }
  };
  const onBonnetClick = (e) => {
    if (selectSrc !== BonnetSrc) {
      setSubmittedOptions([]);
      setChartData(defaultCo2Data);
      setEnergyData(defaultEnergyData);
      setSelectedSrc(BonnetSrc);
      setSelectedPart("BONNET");
    }
  };
  const onBufferClick = (e) => {
    if (selectSrc !== BufferSrc) {
      setSubmittedOptions([]);
      setChartData(defaultCo2Data);
      setEnergyData(defaultEnergyData);
      setSelectedSrc(BufferSrc);
      setSelectedPart("BUMPER");
    }
  };
  const getEnergyData = (fibreMaterialsEnergy) => {
    let W;
    let C;
    if (selectPart === "BONNET") {
      if (selectedResin === 0) {
        W = fibreMaterialsEnergy[selectedFibre].epoxyB;
      } else if (selectedResin === 1) {
        W = fibreMaterialsEnergy[selectedFibre].bioB;
      } else if (selectedResin === 2) {
        W = fibreMaterialsEnergy[selectedFibre].pfaB;
      }
    } else if (selectPart === "FRONT DOOR") {
      if (selectedResin === 0) {
        W = fibreMaterialsEnergy[selectedFibre].epoxyF;
      } else if (selectedResin === 1) {
        W = fibreMaterialsEnergy[selectedFibre].bioF;
      } else if (selectedResin === 2) {
        W = fibreMaterialsEnergy[selectedFibre].pfaF;
      }
    } else if (selectPart === "REAR DOOR") {
      if (selectedResin === 0) {
        W = fibreMaterialsEnergy[selectedFibre].epoxyR;
      } else if (selectedResin === 1) {
        W = fibreMaterialsEnergy[selectedFibre].bioR;
      } else if (selectedResin === 2) {
        W = fibreMaterialsEnergy[selectedFibre].pfaR;
      }
    } else if (selectPart === "BUMPER") {
      if (selectedResin === 0) {
        W = fibreMaterialsEnergy[selectedFibre].epoxyBU;
      } else if (selectedResin === 1) {
        W = fibreMaterialsEnergy[selectedFibre].bioBU;
      } else if (selectedResin === 2) {
        W = fibreMaterialsEnergy[selectedFibre].pfaBU;
      }
    }

    if (rwvalue === 1) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][0];
    } else if (rwvalue === 2) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][1];
    } else if (rwvalue === 3) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][2];
    } else if (rwvalue === 4) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][3];
    } else if (rwvalue === 5) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][4];
    } else if (rwvalue > 5 && rwvalue < 11) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][5];
    } else if (rwvalue > 10 && rwvalue < 21) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][6];
    } else if (rwvalue > 20 && rwvalue < 51) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][7];
    } else if (rwvalue > 50 && rwvalue < 101) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][8];
    } else if (rwvalue > 100 && rwvalue < 201) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][9];
    } else if (rwvalue > 200 && rwvalue < 301) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][10];
    } else if (rwvalue > 300 && rwvalue < 401) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][11];
    } else if (rwvalue > 400 && rwvalue < 501) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][12];
    } else if (rwvalue > 500 && rwvalue < 601) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][13];
    } else if (rwvalue > 600 && rwvalue < 701) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][14];
    } else if (rwvalue > 700 && rwvalue < 801) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][15];
    } else if (rwvalue > 800 && rwvalue < 901) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][16];
    } else if (rwvalue > 900 && rwvalue < 1001) {
      C = manufacturingProcessEnergy[selectedProcess].data[selectedResin][17];
    }

    return W * rwvalue + C;
  };
  const onsubmitClick = () => {
    let W;
    let C;
    const fibreMaterials = getFibreMaterials(selectedProcess);
    const fibreMaterialsEnergy = getFibreMaterialsEnergy(selectedProcess);
    if (selectPart === "BONNET") {
      if (selectedResin === 0) {
        W = fibreMaterials[selectedFibre].epoxyB;
      } else if (selectedResin === 1) {
        W = fibreMaterials[selectedFibre].bioB;
      } else if (selectedResin === 2) {
        W = fibreMaterials[selectedFibre].pfaB;
      }
    } else if (selectPart === "FRONT DOOR") {
      if (selectedResin === 0) {
        W = fibreMaterials[selectedFibre].epoxyF;
      } else if (selectedResin === 1) {
        W = fibreMaterials[selectedFibre].bioF;
      } else if (selectedResin === 2) {
        W = fibreMaterials[selectedFibre].pfaF;
      }
    } else if (selectPart === "REAR DOOR") {
      if (selectedResin === 0) {
        W = fibreMaterials[selectedFibre].epoxyR;
      } else if (selectedResin === 1) {
        W = fibreMaterials[selectedFibre].bioR;
      } else if (selectedResin === 2) {
        W = fibreMaterials[selectedFibre].pfaR;
      }
    } else if (selectPart === "BUMPER") {
      if (selectedResin === 0) {
        W = fibreMaterials[selectedFibre].epoxyBU;
      } else if (selectedResin === 1) {
        W = fibreMaterials[selectedFibre].bioBU;
      } else if (selectedResin === 2) {
        W = fibreMaterials[selectedFibre].pfaBU;
      }
    }

    if (rwvalue === 1) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][0];
    } else if (rwvalue === 2) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][1];
    } else if (rwvalue === 3) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][2];
    } else if (rwvalue === 4) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][3];
    } else if (rwvalue === 5) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][4];
    } else if (rwvalue > 5 && rwvalue < 11) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][5];
    } else if (rwvalue > 10 && rwvalue < 21) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][6];
    } else if (rwvalue > 20 && rwvalue < 51) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][7];
    } else if (rwvalue > 50 && rwvalue < 101) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][8];
    } else if (rwvalue > 100 && rwvalue < 201) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][9];
    } else if (rwvalue > 200 && rwvalue < 301) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][10];
    } else if (rwvalue > 300 && rwvalue < 401) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][11];
    } else if (rwvalue > 400 && rwvalue < 501) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][12];
    } else if (rwvalue > 500 && rwvalue < 601) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][13];
    } else if (rwvalue > 600 && rwvalue < 701) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][14];
    } else if (rwvalue > 700 && rwvalue < 801) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][15];
    } else if (rwvalue > 800 && rwvalue < 901) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][16];
    } else if (rwvalue > 900 && rwvalue < 1001) {
      C = manufacturingProcess[selectedProcess].data[selectedResin][17];
    }

    const data = {
      totalCO2: W * rwvalue + C,
      fibre: fibreMaterials[selectedFibre].name,
      resin: resinMaterials[selectedResin].name,
      process: manufacturingProcess[selectedProcess].name,
      rw: rwvalue,
      totalEnergy: getEnergyData(fibreMaterialsEnergy),
    };

    let tempData = [...submittedOptions];
    if (tempData.length > 3) tempData.shift();
    tempData.push({
      totalCO2: data.totalCO2,
      fibre: data.fibre,
      resin: data.resin,
      process: data.process,
      RW: data.rw,
      totalEnergy: data.totalEnergy,
    });
    setSubmittedOptions(tempData);

    const tempLabels = labelsConst.slice(0, tempData.length);

    const tempCO2ChartData = {
      labels: tempLabels,
      datasets: [
        {
          label: "Greenhouse Gas Emissions (kg CO₂ eq.)",
          data: tempData.map((d) => d.totalCO2),
          backgroundColor: colors.slice(0, tempData.length),
        },
      ],
    };
    const tempEnergyChartData = {
      labels: tempLabels,
      datasets: [
        {
          label: "Cumulative Energy Demand (MJ)",
          data: tempData.map((d) => d.totalEnergy),
          backgroundColor: colors.slice(0, tempData.length),
        },
      ],
    };
    setChartData(tempCO2ChartData);
    setEnergyData(tempEnergyChartData);
    scrollRef.current.scrollIntoView({ behavior: "smooth" });
    // console.log(selectPart, W, selectedProcess);
  };

  return (
    <>
      <NavBar></NavBar>
      {isMobile && <div style={{ height: "500px", background: "red" }}></div>}

      {!isMobile && (
        <MainWrapper>
          <div
            className="container"
            style={{
              padding: "0px 0px ",
              marginTop: "10px",
              fontFamily: "Century Gothic",
            }}
          >
            <div className="row">
              <div
                className="col-xl-6"
                style={
                  {
                    // border: "solid",
                    // borderWidth: "0.01rem",
                    // borderColor: "black",
                  }
                }
              >
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    position: "relative",
                  }}
                >
                  <img
                    onClick={onImageClick}
                    src={CarSrc}
                    useMap="#workmap"
                    style={{
                      width: "560px",
                      position: "relative",
                      // border: "solid",
                    }}
                  />
                  <img
                    src={selectSrc}
                    usemap="#workmap2"
                    style={{
                      width: "560px",
                      position: "absolute",
                    }}
                  />
                  <map name="workmap">
                    <area
                      shape="poly"
                      coords="109,143,127,157,156,177,182,194,184,200,177,226,176,252,180,282,185,305,157,279,122,248,117,241,106,195,104,169,106,149"
                      alt="frontd"
                      onClick={onFrontDClick}
                    />
                    <area
                      shape="poly"
                      coords="59,102,79,119,96,131,105,137,101,155,100,175,102,193,106,209,111,233,111,235,92,220,78,206,68,172,59,148,56,119,56,108"
                      alt="reard"
                      onClick={onRearDClick}
                    />
                    <area
                      shape="poly"
                      coords="224,202,293,184,356,164,399,142,423,130,451,149,480,174,486,191,455,221,381,254,322,264,283,257,234,233,221,221,222,207"
                      alt="bonnet"
                      onClick={onBonnetClick}
                    />
                    <area
                      shape="poly"
                      coords="274,272,300,278,338,282,381,276,428,260,475,234,494,219,508,226,514,249,507,270,506,284,489,319,436,356,365,388,289,391,267,381,261,347,250,317,245,308,257,287"
                      alt="buffer"
                      onClick={onBufferClick}
                    />
                  </map>
                  <map name="workmap2">
                    <area
                      shape="poly"
                      coords="224,202,293,184,356,164,399,142,423,130,451,149,480,174,486,191,455,221,381,254,322,264,283,257,234,233,221,221,222,207"
                      alt="bonnet"
                      onClick={onBonnetClick}
                    />
                    <area
                      shape="poly"
                      coords="59,102,79,119,96,131,105,137,101,155,100,175,102,193,106,209,111,233,111,235,92,220,78,206,68,172,59,148,56,119,56,108"
                      alt="reard"
                      onClick={onRearDClick}
                    />
                    <area
                      shape="poly"
                      coords="109,143,127,157,156,177,182,194,184,200,177,226,176,252,180,282,185,305,157,279,122,248,117,241,106,195,104,169,106,149"
                      alt="frontd"
                      onClick={onFrontDClick}
                    />
                    <area
                      shape="poly"
                      coords="274,272,300,278,338,282,381,276,428,260,475,234,494,219,508,226,514,249,507,270,506,284,489,319,436,356,365,388,289,391,267,381,261,347,250,317,245,308,257,287"
                      alt="buffer"
                      onClick={onBufferClick}
                    />
                  </map>
                </div>
              </div>
              <div
                className="col-lg-6"
                style={{
                  position: "relative",
                  marginTop: "30px",
                  // border: "1px solid",
                }}
              >
                <h5
                  style={{ textAlign: "start", fontFamily: "Century Gothic" }}
                >
                  PLEASE SELECT MATERIALS FOR - {selectPart}
                </h5>
                <>
                  <h6 style={{ marginTop: "20px" }}>REINFORCEMENT</h6>
                  <SelectButton
                    selected={selectedFibre === 0}
                    onClick={() => setSelectedFibre(0)}
                  >
                    Flax Fibre
                  </SelectButton>
                  <SelectButton
                    selected={selectedFibre === 1}
                    onClick={() => setSelectedFibre(1)}
                  >
                    E-Glass Fibre
                  </SelectButton>
                  <SelectButton
                    selected={selectedFibre === 2}
                    onClick={() => setSelectedFibre(2)}
                  >
                    Carbon Fibre
                  </SelectButton>
                </>
                <>
                  <h6 style={{ marginTop: "20px" }}>MATRIX</h6>
                  <SelectButton
                    selected={selectedResin === 0}
                    onClick={() => setSelectedResin(0)}
                  >
                    Epoxy
                  </SelectButton>
                  <SelectButton
                    selected={selectedResin === 1}
                    onClick={() => setSelectedResin(1)}
                  >
                    Bio-based Epoxy
                  </SelectButton>
                  <SelectButton
                    selected={selectedResin === 2}
                    onClick={() => setSelectedResin(2)}
                  >
                    Polyfurfuryl Alcohol (PFA)
                  </SelectButton>
                </>

                <>
                  <h6 style={{ marginTop: "20px" }}>MANUFACTURING PROCESS</h6>
                  <SelectButton
                    selected={selectedProcess === 0}
                    onClick={() => setSelectedProcess(0)}
                  >
                    Vacuum Resin Infusion
                  </SelectButton>
                  <SelectButton
                    selected={selectedProcess === 1}
                    onClick={() => setSelectedProcess(1)}
                  >
                    Prepreg (Oven Vacuum Bag)
                  </SelectButton>
                  <SelectButton
                    selected={selectedProcess === 2}
                    onClick={() => setSelectedProcess(2)}
                  >
                    Prepreg (Compression Moulding)
                  </SelectButton>
                </>

                <div
                  style={{ marginTop: "40px", fontFamily: "Century Gothic" }}
                >
                  <div id="non-linear-slider">
                    Production Volume : {rwvalue}
                  </div>
                  <Slider
                    classes={{
                      root: classes.root,
                    }}
                    size="small"
                    style={{ width: 525 }}
                    min={1}
                    max={1000}
                    step={1}
                    value={rwvalue}
                    onChange={changeRWValue}
                    valueLabelDisplay="on"
                    getAriaValueText={getText}
                    valueLabelFormat={getText}
                    aria-labelledby="discrete-slider-restrict"
                    // marks={marks}
                    aria-label="Small"
                    // color="secondary"
                  />
                </div>
                <SubmitButton
                  className="btn btn-success"
                  onClick={onsubmitClick}
                >
                  FIND SUSTAINABLE DESIGN
                </SubmitButton>
              </div>
            </div>
            <div className="row" style={{ margin: "80px 0 50px 0" }}>
              <div className="col-xl-6" ref={scrollRef}>
                <Bar data={chartData} options={options} height={200} />
              </div>
              <div className="col-xl-6">
                <Bar data={energyData} options={optionsEnergy} height={200} />
              </div>
            </div>
            <div className="row">
              <table
                style={{
                  marginBottom: "50px",
                  fontFamily: "Century Gothic",
                  fontSize: "18px",
                  marginLeft: "35px",
                }}
              >
                <tr>
                  <th style={{ width: "90px" }}>Option</th>
                  <th style={{ width: "90px" }}>Reinforcement</th>
                  <th style={{ width: "90px" }}>Matrix</th>
                  <th style={{ width: "90px" }}>Volume </th>
                  <th style={{ width: "210px" }}>Manufacturing Process</th>
                  <th style={{ width: "90px" }}>Colour</th>
                </tr>
                {submittedOptions.map((option, i) => (
                  <tr key={i}>
                    <td>Option {i + 1}</td>
                    <td>{option.fibre}</td>
                    <td>{option.resin}</td>
                    <td>{option.RW}</td>
                    <td>{option.process}</td>
                    <td>
                      <div
                        style={{
                          width: "50px",
                          height: "20px",
                          backgroundColor: colors[i],
                        }}
                      ></div>
                    </td>
                  </tr>
                ))}
              </table>
            </div>
          </div>
        </MainWrapper>
      )}
      <FooterContainer>
        <p>
          Far-UK's data are based on sourced references. The calculations
          pertain to a specific concept design for an automotive vehicle and
          compare concepts with equivalent mechanical performance
        </p>
      </FooterContainer>
    </>
  );
}
